import RaTe from "../../../components/uni-ui/uni-rate/uni-rate.vue"	
	 export default{
		 components:{
			RaTe 
		 },
		 data(){
			 return{
				 height:'',
				 color:'#f2f2f2',
				 activeColor:"#eb828f",
				 size:"30",
				 ss:"",
				 pjlist:[
					 {name:"差"},
					 {name:"一般"},
					 {name:"好"},
					 {name:"很好"},
					 {name:"非常好"},
				 ],
				evalcontent:"",
				imglist:"",
				imgtrue:true,
				ok:true,
				nmtitle:"你写的评价会以匿名的形式展现",
				src:"",
				srctrue:false,
			 }
		 },
		 methods:{
			 chang($event){ 
			 },
			 chang1($event){
			 },
			 chang2($event){
			 },
			 hehe(){
				console.log(123456); 
			 },
			 scimg(){
			 				 var that=this;
			 				if(that.imgtrue==false){ 
			 				 uni.chooseImage({
			 				 	count:1,
			 					sizeType:"compressed",
			 					success:function(res){
			 				    that.imgtrue=true;		
			 					that.imglist=res.tempFilePaths;	   
			 					}
			 				 })
			 				 }else{
			 				  uni.previewImage({
			 				              urls:that.imglist,
			 								   })
			 				 }
			 },
			 scvideo(){
				    var that=this;
				           uni.chooseVideo({
				                           count: 1,
				                           sourceType: ['camera', 'album'],
				                           success: function (res) {
											   that.srctrue=true;
				                               that.src = res.tempFilePath;
				                           }
				                       });
				      
				        
				    
			 },
			 nmchange(){
				 this.ok=!this.ok;
			  	 if(this.ok==true){
					 this.nmtitle="你写的评价会以匿名的形式展现"
				 }else{
					this.ok==false;
					this.nmtitle="公开的评价会展示在个人页面"
				 }
			 },
		 }
		 
		}
	 
